import createReducer from '../createReducer';
import { ActionType, defaultAppState } from "../types";
import { RedelkInitStatus } from "../../types";
const initialState = {
  showTopNav: false,
  currentRoute: "",
  initStatus: RedelkInitStatus.idle,
  appState: defaultAppState,
  topNavMenu: []
};
export const configReducer = createReducer(initialState, {
  [ActionType.CONFIG_REDELK_INIT_CHECK](state, action) {
    return { ...state,
      initStatus: RedelkInitStatus.pending
    };
  },

  [ActionType.CONFIG_REDELK_INIT_CHECK_SUCCESS](state, action) {
    return { ...state,
      status: RedelkInitStatus.success,
      initStatus: null
    };
  },

  [ActionType.CONFIG_REDELK_INIT_CHECK_FAILURE](state, action) {
    return { ...state,
      initStatus: RedelkInitStatus.failure
    };
  },

  [ActionType.CONFIG_SHOW_TOP_NAV](state, action) {
    return { ...state,
      showTopNav: action.payload
    };
  },

  [ActionType.CONFIG_SET_CURRENT_ROUTE](state, action) {
    return { ...state,
      currentRoute: action.payload
    };
  },

  [ActionType.CONFIG_SET_IS_INITIALIZED](state, action) {
    return { ...state,
      isInitialized: action.payload
    };
  },

  [ActionType.CONFIG_SET_APPSTATE](state, action) {
    return { ...state,
      appState: action.payload
    };
  },

  [ActionType.CONFIG_SET_TOPNAVMENU](state, action) {
    return { ...state,
      topNavMenu: action.payload
    };
  }

});