import { call, fork, put, takeLatest } from 'redux-saga/effects';
import { ActionType } from '../types';

const callSearchPromise = ({
  fn,
  opts
}) => {
  return fn(opts).toPromise();
};

function* fetchAllRtops({
  payload: {
    data,
    searchOpts
  }
}) {
  try {
    const response = yield call(callSearchPromise, {
      fn: data.search.search,
      opts: searchOpts
    });
    yield put({
      type: ActionType.RTOPS_FETCH_ALL_REQUEST_SUCCESS,
      payload: response.rawResponse
    });
  } catch (e) {
    console.error(e);
    yield put({
      type: ActionType.RTOPS_FETCH_ALL_REQUEST_FAILURE,
      payload: e
    });
  }
}

function* createIoc({
  payload: {
    http,
    payload
  }
}) {
  try {
    const response = yield call(http.post, {
      path: '/api/redelk/ioc',
      body: JSON.stringify(payload)
    });
    yield put({
      type: ActionType.RTOPS_CREATE_IOC_REQUEST_SUCCESS,
      payload: response.rawResponse
    });
  } catch (e) {
    console.error(e);
    yield put({
      type: ActionType.RTOPS_CREATE_IOC_REQUEST_FAILURE,
      payload: e
    });
  }
}

function* onSetRtopsWatcher() {
  yield takeLatest(ActionType.RTOPS_FETCH_ALL_REQUEST, fetchAllRtops);
}

function* onCreateIOCWatcher() {
  yield takeLatest(ActionType.RTOPS_CREATE_IOC_REQUEST, createIoc);
}

export default [fork(onSetRtopsWatcher), fork(onCreateIOCWatcher)];