import { call, fork, put, takeLatest } from 'redux-saga/effects';
import { ActionType } from '../types';

function* checkInit({
  http
}) {
  try {
    const response = yield call(http.get, {
      path: '/api/redelk/init'
    });
    yield put({
      type: ActionType.CONFIG_REDELK_INIT_CHECK_SUCCESS,
      payload: response.rawResponse
    });
  } catch (e) {
    console.error(e);
    yield put({
      type: ActionType.CONFIG_REDELK_INIT_CHECK_FAILURE,
      payload: e
    });
  }
}

function* onCheckInitWatcher() {
  yield takeLatest(ActionType.CONFIG_REDELK_INIT_CHECK, checkInit);
}

export default [fork(onCheckInitWatcher)];