import { ActionType } from "../types";
export const setShowTopNav = payload => {
  return {
    type: ActionType.CONFIG_SHOW_TOP_NAV,
    payload
  };
};
export const setCurrentRoute = payload => {
  return {
    type: ActionType.CONFIG_SET_CURRENT_ROUTE,
    payload
  };
};
export const checkInit = http => {
  return {
    type: ActionType.CONFIG_REDELK_INIT_CHECK,
    http: http
  };
};
export const setAppState = payload => {
  return {
    type: ActionType.CONFIG_SET_APPSTATE,
    payload
  };
};
export const setTopNavMenu = payload => {
  return {
    type: ActionType.CONFIG_SET_TOPNAVMENU,
    payload
  };
};