import createReducer from '../createReducer';
import { ActionType } from "../types";
import { KbnCallStatus } from "../../types";
const initialState = {
  status: KbnCallStatus.idle,
  error: null,
  rtops: undefined,
  showAddIOCForm: false,
  lastRefresh: undefined
};
export const rtopsReducer = createReducer(initialState, {
  [ActionType.RTOPS_FETCH_ALL_REQUEST](state, action) {
    return { ...state,
      status: KbnCallStatus.pending
    };
  },

  [ActionType.RTOPS_FETCH_ALL_REQUEST_SUCCESS](state, action) {
    return { ...state,
      rtops: action.payload,
      status: KbnCallStatus.success,
      error: null,
      lastRefresh: new Date()
    };
  },

  [ActionType.RTOPS_FETCH_ALL_REQUEST_FAILURE](state, action) {
    return { ...state,
      status: KbnCallStatus.failure,
      error: action.payload
    };
  },

  [ActionType.RTOPS_CREATE_IOC_REQUEST](state, action) {
    return { ...state
    };
  },

  [ActionType.RTOPS_CREATE_IOC_REQUEST_SUCCESS](state, action) {
    return { ...state,
      status: KbnCallStatus.success,
      error: null
    };
  },

  [ActionType.RTOPS_CREATE_IOC_REQUEST_FAILURE](state, action) {
    return { ...state,
      status: KbnCallStatus.failure,
      error: action.payload
    };
  },

  [ActionType.RTOPS_SHOW_ADD_IOC_FORM](state, action) {
    return { ...state,
      showAddIOCForm: action.payload
    };
  }

});
export default rtopsReducer;